@import "../index.sass";
.start-page {
    position: relative;
    width: 100vw;
    min-height: 100vh;
    @include flexCenter();
    background-color: $baseDark;
    transition: .5s;
    .credits {
        position: absolute;
        top: 20px;
        left: 20px;
        width: auto;
        @media (max-width: 1000px) {
            visibility: hidden; }
        a {
            text-decoration: none;
            color: #fff;
            padding: 3px 5px;
            border: 2px solid #fff;
            border-radius: 8px;
            background-color: red;
            animation: borderChange 4s linear infinite;
            @keyframes borderChange {
                25% {
                    background-color: green; }
                50% {
                    background-color: orange; }
                75% {
                    background-color: blue; }
                100% {
                    background-color: red; } } } }
    .header {
        @include flexCenter();
        width: 100%;
        text-align: center;
        .brainContainer {
            position: absolute;
            left: 50%;
            top: 100px;
            width: 200px;
            transform: translateX(-50%);
            opacity: 0;
            animation: brainFadeIn .5s forwards 1.2s;
            @keyframes brainFadeIn {
                100% {
                    opacity: 1; } }
            @media (max-width: 1000px) {
                width: 120px;
                top: 70px; }
            .brain {
                width: 100%; }
            .idea {
                position: absolute;
                top: -75px;
                left: 80px;
                animation: idea 1s linear infinite;
                @keyframes idea {
                    50% {
                        transform: scale(1.2); }
                    100% {
                        transform: none; } }
                @media (max-width: 1000px) {
                    width: 40px;
                    top: -50px;
                    left: 50px; } } }

        h1 {
            width: 100%;
            @include flexCenter();
            font-size: 60px;
            z-index: 1;
            background-color: $baseDark;
            span {
                display: block;
                margin: 20px 5px;
                width: 70px;
                height: 100px;
                @include flexCenter();
                border-radius: 8px;
                font-size: 60px;
                background-color: $cream;
                color: $baseDark;
                @media (max-width: 1000px) {
                    width: 60px;
                    height: 80px; }
                @media (max-width: 450px) {
                    width: 45px;
                    height: 60px;
                    font-size: 45px; }
                @media (max-width: 330px) {
                    width: 38px;
                    height: 55px;
                    font-size: 45px; } }
            span:nth-of-type(odd) {
                transform: rotate(5deg) scale(0);
                animation: OddLetterAppear .7s forwards;
                @keyframes OddLetterAppear {
                    100% {
                        transform: rotate(5deg) scale(1); } } }
            span:nth-of-type(even) {
                transform: rotate(-5deg) scale(0);
                animation: EvenLetterAppear .5s forwards;
                @keyframes EvenLetterAppear {
                    100% {
                        transform: rotate(-5deg) scale(1); } } }
            span:nth-of-type(2) {
                animation-delay: .2s; }
            span:nth-of-type(3) {
                animation-delay: .4s; }
            span:nth-of-type(4) {
                animation-delay: .3s; }
            span:nth-of-type(5) {
                animation-delay: .1s; }
            span:nth-of-type(6) {
                animation-delay: .5s; } }
        .letters {
            margin-top: 170px; }
        .letters-2 {
            margin-bottom: 10px; }
        a {
            text-decoration: none;
            color: #fff; }
        p {
            @media (max-width: 1000px) {
                visibility: hidden; } } }
    .scrollDown {
        position: absolute;
        top: 85vh;
        left: 20px;
        @include buttons();
        width: 35px;
        height: 35px;
        border-radius: 50%;
        z-index: 1;
        @include flexCenter();
        font-size: 18px;
        visibility: {}
        @media (max-width: 100px) {
            visibility: visible; }
        .fas {
            letter-spacing: 0; } }
    .inputs {
        width: 30%;
        @include flexCenter();
        @media (max-width: 1000px) {
            width: 100%;
            margin-bottom: 20px; }
        h3 {
            width: 100%;
            text-align: center;
            margin-bottom: 20px;
            font-size: 25px; }
        select, button {
            height: 30px;
            @include buttons(); }
        select {
            padding-left: 10px; }
        button {
            width: 90px;
            padding: 0 10px;
            margin: 0 10px;
            cursor: pointer;
            @media (max-width: 450px) {
                margin-bottom: 10px; } }
        button.active {
            box-shadow: 0px 0px 10px 5px $cream; } }
    .startContainer {
        width: 100%;
        text-align: center;
        @media (max-width: 1000px) {
            margin-bottom: 20px; }
        button {
            @include buttons();
            height: 60px;
            width: 170px;
            font-size: 30px;
            @media (max-width: 1000px) {
                width: 140px;
                height: 45px;
                font-size: 25px; } } } }
.disappear {
    transform: scale(0); }
